@import '../../generals';

.tecnologias {
  width: 100%;
  padding: 5rem 0;
  text-align: center;

  h2 {
    border-bottom: 3px solid;
    display: inline-block;
    text-align: center;
    font-size: 2rem;
  }

  &__list {
    margin: 5rem 0;
    display: flex;
    flex-wrap: wrap;
    gap: 2rem;
    place-content: center;
    li {
      list-style: none;
      display: flex;
      flex-direction: column;
      gap: 1rem;
      align-items: center;
      img {
        display: block;
        max-width: 8rem;
        height: 8rem;
        transition: transform 500ms ease;
      }

      span {
        opacity: 0;
        text-align: center;
        transition: opacity 500ms ease;
      }

      &:hover {
        span {
          opacity: 0.7;
        }

        img {
          transform: scale(1.2);
        }
      }
    }
  }

  @media screen and (max-width: 630px) {
    & {
      h2 {
        border-color: transparent;
      }

      &__list {
        li {
          gap: 0.25rem;

          img {
            width: 5rem;
            height: 5rem;
          }
        }
      }
    }
  }
}
